.toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #F6F0F2;
    height: 56px;
}

.toolbar_navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.toolbar_logo {
    margin-left: 1rem;;
}

.toolbar_logo a {
    color:#5792D5;
    text-decoration: none;
    font-size: 1.5rem;
}

.toolbar_logo a img {
    height: 50%;
    width: 50%;
}

.spacer {
    flex:1;
}

.toolbar_navigation-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar_navigation-items li {
    padding: 0 0.5rem;
}

.toolbar_navigation-items a {
    color: #5792D5;
    text-decoration: none;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active{
    color:#A89694;
}

/* Media Quires */
@media (max-width: 768px) {
    .toolbar_navigation-items {
        display: none;
    }
}
@media (min-width: 769px) {
    .toolbar__toggle-button {
        display: none;
    }

    .toolbar_logo {
        margin: 0;
    }
}